.faq {
  &__item {
    border-bottom: 1px solid currentColor;
    &:first-child {
      border-top: 1px solid currentColor; } } }

.faq-question {
  position: relative;

  @include before {
    @include icon-arrow(.8, .8, .07, currentColor, -45, em);
    position: absolute;
    top: rem(20);
    right: rem(20);
    z-index: 1; }

  &__title, &__content {
    padding-left: rem(20);
    padding-right: rem(100);
    @include sm {
      padding-left: rem(10, 14);
      padding-right: rem(80, 14); }
    @include xs {
      padding-left: rem(5, 14); } }

  &__title {
    display: block;
    width: 100%;
    text-align: left;

    padding-top: rem(20);
    padding-bottom: rem(20); }

  &__content {}


  &--link {
    @include before {
      @include rotate(225);
      top: rem(24); } } }

