.card {
  display: block;
  overflow: hidden;
  border-radius: 3px;
  box-shadow: 0px 0.665031px 6.65031px rgba(0, 0, 0, 0.1);
  max-width: 240px;

  &__img {
    @include cover;

    background-color: $light;
    padding-top: 45%; }

  &__content {
    padding: rem(13) rem(20) rem(20); }

  &__title {
    font-weight: 700;
    font-size: em(20);
    margin-bottom: rem(8);
    span {
      color: #43484D;
      opacity: .6; } }

  &__meta {
    display: flex;
    align-items: center;
    justify-content: space-between; }

  &__percent {
    display: flex;
    align-items: center;

    font-weight: 700;
    position: relative;

    @include before {
      @include arr(.5em, .5em, currentColor, t);
      display: inline-block;
      margin-right: .3em; }

    &--down {
      color: #F90000; }
    &--up {
      color: #7DC855; } } }


